<script>
    import CardUnit from "./CardUnit.svelte";
    import { items } from "./../stores/answerStore.js";
    import { searchStore } from "../stores/searchStore.js";
    import Searchbar from "./Searchbar.svelte";
    import Charts from "./Charts.svelte";

    let listItems = $items || [];

    // Search

    const removeAccents = (str) => {
        return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    };

    $: listFiltered = listItems.filter(
        (item) =>
            removeAccents(item.Tribu.toLowerCase()).includes(
                removeAccents($searchStore.toLowerCase())
            ) ||
            removeAccents(item.Nombre.toLowerCase()).includes(
                removeAccents($searchStore.toLowerCase())
            )
    );

    $: listCrop = listFiltered.map((item, i, array) => {
        delete item.ID;
        delete item["Hora de finalización"];
        delete item["Hora de inicio"];

        return array;
    });

    let toggleVisible = true;

    let toggleMenu = "Personas";
</script>

<style>
    .invisible {
        display: none;
    }
</style>

<CardUnit />

<div>
    {#each listFiltered as item}
        <CardUnit
            name={item.Nombre}
            tribal={item.Tribu}
            email={item['Correo electrónico']}
            itemKey={Object.keys(item)}
            values={Object.values(item)} />

        {#each Object.keys(item) as key}
            {key}

            <div
                class="mt3 ph3 pt2 pb2 shadow-5"
                class:invisible={toggleVisible}>
                <p class="b">{key}:</p>
                <p>{item[key]}</p>

                <p class=" f6 tr mb1">
                    <select name="moderar" id="moderar">
                        <option value="Moderar">Moderar</option>
                        <option value="0">0</option>
                        <option value="0,25">0,25</option>
                        <option value="0,50">0,50</option>
                        <option value="0,75">0,75</option>
                        <option value="1">1</option>
                    </select>
                </p>
                <p />
            </div>
        {/each}
    {/each}

    {#each listFiltered as item, i}
        <article class=" center mw7  br3 hidden ba b--black-10 mv4">
            <h1 class="f4 bg-near-white br3 br--top black-60 mv0 pv2 ph3 tl">
                PO:
                {item.Nombre}
                | {item.Tribu}

                <input
                    class="fr"
                    type="checkbox"
                    checked
                    id="visible"
                    name="visible" />
            </h1>
            <div class="tl pa3 bt b--black-10">
                <p class="f6 f5-ns lh-copy measure">
                    <span class="b">Puntuación Obtenida:</span>
                    {Math.random(0, 1).toFixed(2)}
                </p>
                <p class="f6 f5-ns lh-copy measure">
                    {item['Correo electrónico']}
                </p>

                <div class="">
                    <button
                        on:click={() => {
                            toggleVisible = !toggleVisible;
                        }}>ver más información...</button>
                </div>
            </div>
        </article>
    {/each}
</div>
